.nav {

  &__list {
    display: flex;
  
    list-style: none;
    text-transform: uppercase;
  
    &-item {
      list-style: none;
    }
  
    &-link {
      margin: 5px 10px;
      padding: 23px 0;
  
      font-size: 12px;
      line-height: 60px;
      font-weight: 500;
      color: $main-dark-blue;
  
      text-decoration: none;
      transition: color 0.3s;
  
      &:hover {
        color: $accent-blue;
      }
    }
  }
}
