.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;

    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;

    &:last-child {
      margin-right: 0;
    }
  }
}

@for $index from 1 to 5 {
  .stars--#{$index} > .stars__star:nth-child(-n + #{$index}) {
    background-image: url(../images/star-active.svg);
  }
}
